body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

/***** basic style *****/
img {
  max-width: 100%;
  -o-object-fit: contain;
     object-fit: contain;
}

a {
  text-decoration: none;
  color: inherit;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  max-width: 90% !important;
}

.border-bottom {
  border-bottom: 1px solid #eee;
}

.container {
  max-width: 96% !important;
}

/***** basic fonts *****/
.font-size-xs {
  font-size: 0.8em;
}

.font-size-sm {
  font-size: 0.9em;
}

.font-size-normal {
  font-size: 1em;
}

.font-size-md {
  font-size: 1.1em;
}

.font-size-bg {
  font-size: 1.2em;
}

.font-size-l {
  font-size: 1.3em;
}

.font-size-xl {
  font-size: 1.5em;
}

.font-weight-md {
  font-weight: 500;
}

.font-weight-bg {
  font-weight: 600;
}

.font-weight-xl {
  font-weight: 700;
}

/*** backgrounds ***/
.bg-none {
  background: #fff;
}

.bg-red {
  background: red;
}

.bg-orange {
  background: orange;
}

.bg-green {
  background: green;
}

.bg-blue {
  background: blue;
}

.bg-black {
  background: #000000;
}

/*** basic btn style ***/
.btn {
  border-radius: 0.25rem !important;
}

.btn:active, .btn:focus, .btn:hover {
  outline: none !important;
  -webkit-box-shadow: none;
          box-shadow: none;
}

/**** forms *****/
.form-control {
  border-radius: 4px !important;
}

.form-control-with-prepend {
  border-radius: 0px 4px 4px 0px !important;
}

.input-group-prepend {
  border: 1px solid #d3d3d3 !important;
  border-radius: 4px 0px 0px 4px;
}


.app-spinner {
  width: 100px;
  height: 100px;
  position: absolute;
  left: 50%;
  top: 50%;
  margin-left: -50px;
  margin-top: -50px;
}
/* minimizing all containers padding for small screens */
@media (max-width: 500px) {
  .container {
    padding: 0.5em !important;
  }
}
